import React from 'react';
import Notification from '../AskForNotification/AllowNotification.js';
import swal from 'sweetalert';

class Home extends React.Component {
    constructor(props) {
        super(props);
        this.state = { 
            askPermission:false
         }
    }
    componentDidMount(){
        swal("Please Allow Safe Basket To Send You Notifications", {
            buttons: ["Not Now", "Aww yiss!"],
            closeOnClickOutside: false
        }).then(res =>{
            (res)? this.setState({ askPermission : true }) : this.setState({ askPermission : false })
        })
        .catch(err => {

        })
    }
    render() { 
        return ( 
            <React.Fragment>
                <h1 align='center'>Home</h1>
                {
                    (this.state.askPermission)?<Notification  /> : <div></div>
                }
            </React.Fragment>
         );
    }
}

export default Home;